.ul {
  position: absolute;
  padding: 0;
  margin: 0;
  bottom: -40px;
  width: 230px;
  left: -70px;
  list-style: none;
  z-index: 999;
}
.link {
  font-size: 12px;
  font-weight: 300;
  color: #ffffff;
  text-decoration: none;
}
.link:hover, .link:focus, .link:active {
  color: #ffffff;
  text-decoration: none;
  outline: none;
}
.li:not(:last-child) {
  padding-bottom: 30px;
}
@media (min-width: 1200px) {
  .ul {
    left: 0;
    bottom: 1vh;
    width: auto;
  }
  .link {
    font-size: 24px;
    transition: all .3s ease;
    cursor: pointer;
  }
  .link:hover {
    color: #69c9ca;
  }
  .li:not(:last-child) {
    margin-bottom: 2vh;
  }
  .li:not(:last-child) {
    padding-bottom: 0;
  }
}
