.enter {
  opacity: 0;
}

.enterActive {
  opacity: 1;
  transition: opacity 200ms ease-in-out;
}

.exit {
  opacity: 1;
}

.exitActive {
  opacity: 0;
  transition: opacity 200ms ease-in-out;
}

@media(min-width: 1200px) {
  .enter {
    opacity: 0;
    transform: translateX(100%);
  }

  .enterActive {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 200ms ease-in-out, transform 200ms ease-in-out;
  }

  .exit {
    opacity: 1;
    transform: translateX(0);
  }

  .exitActive {
    opacity: 0;
    transform: translateX(100%);
    transition: opacity 200ms ease-in-out, transform 200ms ease-in-out;
  }
}
